<template>
    <div>
        <h1>购物车</h1>
        <div class="mui-card">
			<div class="mui-card-content">
				<div class="mui-card-content-inner">
					<div class="info">
                        <h1></h1>
                       
                            
                             <div class="mui-numbox" :data-numbox-min='this.$store.state.number' style="height:25px;">
                                <button class="mui-btn mui-btn-numbox-minus" type="button">-</button>
                                <input id="test" class="mui-input-numbox" type="number" @change="countChanged"  ref="numbox" readonly />
                                <button class="mui-btn mui-btn-numbox-plus" type="button">+</button>
                            </div>
                            <!-- <a href="#" >删除</a> -->
                        
                    </div>	

                </div>
            </div>
        </div>
    </div>
</template>

<script>
import mui from '@/assets/mui/js/mui.min.js'
export default {
    mounted(){
        mui(".mui-numbox").numbox()
    },
    methods:{
        countChanged(){
            let numbox=this.$refs.numbox.value
            this.$store.commit("updatanumber",numbox)
        }
    }
    
}
</script>

<style scoped>

</style>




