<template>
    <div class="zpxx">
        <header class="mui-bar mui-bar-nav mui-bar-blue">
            <a class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left"></a>
			<h1 class="mui-title">招聘信息</h1>
		</header>

        <div class="mui-card">
            <div class="mui-card-content">
                <div class="mui-card-content-inner">
                    <img :src="list.url" alt=""> 
                </div>
            </div>
        </div>

        <div class="mui-card">
            <div class="mui-card-header">
                <h3>{{list.f_name}}</h3>
            </div>
            <div class="mui-card-content">
                <div class="mui-card-content-inner">
                    <h4>公司介绍：</h4>
                    {{list.f_introduce}}
                </div>
            </div>
        </div>
        <div class="mui-card">
				<div class="mui-card-header">招聘信息</div>
				<div class="mui-card-content">
					<div class="mui-card-content-inner">
                        <span>职位：{{list.r_position}}</span>
                        <span>工资：{{list.r_wages}}</span>
                        <span>学历：{{list.r_education}}</span>
                        <span>经验：{{list.r_experience}}</span>
					</div>
				</div>
				<div class="mui-card-footer">
                    <h4>工作要求：{{list.r_introduce}}</h4>
                </div>
			</div>
    </div>

    
</template>

<script>

export default {
    data(){
        return {
            id :this.$route.params.id,
            list:{}
        }
    },
    created(){
        
         this.getzpxx()
    }, 
    methods:{
        getzpxx(){
             this.$http.get('http://47.102.142.80:8080/qzzp/dzsj.do').then(result=>{
               let index=result.body.findIndex(item=>{ 
                   if(item.r_id==this.id){
                       return true
                   }
               })
               this.list=result.body[index]
               this.list.url="http://47.102.142.80:8080"+this.list.f_logourl
               console.log(this.list)
                
            })
            
        }
    }
    
}
</script>

<style scoped>
     * { touch-action: pan-y; } 
    .zpxx{
        background: #fafafa;
        overflow: hidden;
    }
    header a{
        color: #fff;
    }
    .mui-card{
        width:355px;
    }
    
     img{
        width:100%;
        height:200px;
    }
    .mui-card-content-inner span{
        display: inline-block;
        width:150px;
    }
</style>



