<template>
    <div>
        <ul class="mui-table-view"> 
            <li class="mui-table-view-cell mui-border-bottom" v-for="data in list" :key="data.id">
                <div class="mui-media-body recruit" >
                    <a href="javascript:;" class="r_id" @click="tozpxx(data.id)">{{data.position}}</a>
                    <span>{{data.wages}} &#124; {{data.city}} &#124; {{data.education}} &#124; {{data.experience}}</span>
                    <a href="javascript:;" class="f_name">{{data.name}}</a>
                </div>
                <button type="button" class="mui-btn mui-btn-primary mui-btn-outlined delivery" >投递简历</button>
            </li>
        </ul>  
    </div>
</template>

<script>
export default {
    props:["list"],
    methods:{
        tozpxx(id){
            this.$router.push({name:'tozpxx',params:{id}})
        } 
    }
}
</script>

<style>
     .mui-border-bottom{
        border-bottom:1px solid #c0c0c0 ;
    }
    .mui-table-view-cell .recruit {
        width:400px;
    }
    .mui-table-view-cell .recruit a{
        display:inline-block;
        width:400px;
        margin: 10px 5px;
        font-size:25px;
    }
    
    .mui-table-view-cell .recruit a:hover{
        color:#007aff;
    } 
    .mui-table-view-cell .recruit span{
        display:inline-block;
        margin-left: 5px;
        font-size:15px;
        color: #aaa ;
    }
    .mui-table-view-cell .recruit .f_name{
        display:inline-block;
        width:400px;
        margin: 10px 5px 0;
        font-size:17px;
    }
    
    .mui-table-view-cell .mui-btn{
        width: 100px;
        height: 40px;
        font-size: 18px;
    }

</style>



