import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

var store=new Vuex.Store({
    state:{
        number:1
    },
    mutations:{
        updatanumber(state,number){
            state.number=number
        }
    },

})

export default store


