<template>
    <div>
        <div id="slider" class="mui-slider mui-fullscreen">
            <div id="sliderSegmentedControl" class="mui-scroll-wrapper mui-slider-indicator mui-segmented-control mui-segmented-control-inverted">
                <div class="mui-scroll">
                    <a class="mui-control-item mui-active" href="#itemmobile1">
                        java开发
                    </a>
                    <a class="mui-control-item" href="#itemmobile2" >
                        UI设计师
                    </a>
                    <a class="mui-control-item" href="#itemmobile3" >
                        php开发
                    </a>
                    <a class="mui-control-item" href="#itemmobile4" >
                        web开发
                    </a>
                    <a class="mui-control-item" href="#itemmobile5" >
                        puthon开发
                    </a>
                </div>
            </div>
            <div class="mui-slider-group">
                <div id="itemmobile1" class="mui-slider-item mui-control-content">
                    <div id="scroll3" class="mui-scroll-wrapper">
                        <div class="mui-scroll">
                            <sjlb :list="list"></sjlb>
                        </div>
                    </div>
                        
                </div>
                <div id="itemmobile2" class="mui-slider-item mui-control-content">
                    <div id="scroll3" class="mui-scroll-wrapper">
                        <div class="mui-scroll">
                            <sjlb :list="list"></sjlb>
                        </div>
                    </div>
                        
                </div>
                <div id="itemmobile3" class="mui-slider-item mui-control-content">
                    <div id="scroll3" class="mui-scroll-wrapper">
                        <div class="mui-scroll">
                            <sjlb :list="list"></sjlb>
                        </div>
                    </div>
                        
                </div>
                <div id="itemmobile4" class="mui-slider-item mui-control-content">
                    <div id="scroll3" class="mui-scroll-wrapper">
                        <div class="mui-scroll">
                            <sjlb :list="list"></sjlb>
                        </div>
                    </div>
                        
                </div>
                <div id="itemmobile5" class="mui-slider-item mui-control-content">
                    <div id="scroll3" class="mui-scroll-wrapper">
                        <div class="mui-scroll">
                            <sjlb :list="list"></sjlb>
                        </div>
                    </div>
                        
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import mui from '@/assets/mui/js/mui.min.js'
import sjlb from '@/components/home/sjlb.vue'
  
export default {
    data(){
        return {
            list:[]
        }
    },
    created(){
        this.getlunbotu();
    },
    mounted(){
        mui('.mui-scroll-wrapper').scroll({
            indicators: false, //是否显示滚动条
            bounce: true ,//是否启用回弹
            deceleration: 0.0005 //flick 减速系数，系数越大，滚动速度越慢，滚动距离越小，默认值0.0006
        });
    },
    methods:{
         getlunbotu(){
            this.$http.get('http://47.102.142.80:8080/qzzp/dzsj.do').then(result=>{
               
                for(let a of result.body){
                    let doc={}
                    doc.position=a.r_position
                    doc.wages=a.r_wages
                    doc.city=a.r_city
                    doc.education=a.r_education
                    doc.experience=a.r_experience
                    doc.name=a.f_name
                    doc.id=a.r_id
                    doc.url="http://47.102.142.80:8080"+a.f_logourl
                    this.list.push(doc)
                }
                console.log( this.list)
            })
            
        } 
    },
    components:{
        sjlb
    }
}
</script>

<style scoped>
    * {
        touch-action: pan-y;
    }
    #slider{
        border-bottom:1px solid #c0c0c0;
        overflow: hidden;
    }
    .mui-fullscreen{
         margin: 44px 0 50px 0;
    }
</style>




