<template>
    <div>
        <!-- 轮播图区域 -->
        <lunbotu :list="list"></lunbotu>

        <router-link to="/home/rmzw" class="rmzw">热门职位</router-link>
        <sjlb :list="list"></sjlb>
        
    </div>    
</template>

<script>

import lunbotu from '@/views/lunbotu.vue'
import sjlb from '@/components/home/sjlb.vue'  
export default{
    data(){
        return {
            list:[]
        }
    },
    created(){
        this.getlunbotu();
    },
    methods:{
        getlunbotu(){
            this.$http.get('http://47.102.142.80:8080/qzzp/dzsj.do').then(result=>{
               
                for(let a of result.body){
                    let doc={}
                    doc.position=a.r_position
                    doc.wages=a.r_wages
                    doc.city=a.r_city
                    doc.education=a.r_education
                    doc.experience=a.r_experience
                    doc.name=a.f_name
                    doc.id=a.r_id
                    doc.url="http://47.102.142.80:8080"+a.f_logourl
                    this.list.push(doc)
                }
                 
                console.log( this.list)
            })
            
        } 
    },
    components:{
        lunbotu,
        sjlb
    }
   
}
</script>

<style scoped>
    .mui-border-bottom{
        border-bottom:1px solid #c0c0c0 ;
    }
 
    .rmzw{
        display: inline-block;
        font-size: 25px;
        margin: 10px auto;
        width: 100%;
        line-height: 30px;
        text-align: center;

    }
</style>



